<template>
    <div>
        <b-row>
            <b-col lg="4">
                <div id="stage-parent">
                    <div id="container" class="border">
                        <v-stage ref="stage" @click="onStageClick" @mousemove="onStageMouseMove"
                            @mouseenter="onStageMouseenter" @mouseleave="onStageMouseleave" :config="configKonva">
                            <v-layer ref="layer">
                                <v-rect v-for="(x, k) in items" :key="k" :config="x" @dragstart="handleDragstart"
                                    @dragend="handleDragend" />
                            </v-layer>
                        </v-stage>
                    </div>
                </div>
            </b-col>
            <b-col lg="8">
                right
            </b-col>
        </b-row>
    </div>
</template>
<script>
var sceneWidth = 1500;
var sceneHeight = 1000;
var stage = null;
var layer = null;
function generateItems() {
    let width = sceneWidth - 80;
    let height = sceneHeight - 80;
    const items = [];
    for (let i = 0; i < 1500; i++) {
        items.push({
            x: Math.random() * width,
            y: Math.random() * height,
            radius: 50,
            id: "node-" + i,
            width: 100,
            height: 100,
            fill: Konva.Util.getRandomColor(),
            stroke: 'black', //Konva.Util.getRandomColor(),
            draggable: true,
            strokeWidth: 4,
        });
    }
    return items;
}
export default {
    data() {
        return {
            configKonva: {
                width: sceneWidth,
                height: sceneHeight
            },

            rect: {
                x: 0,
                y: 0,
                width: 100,
                height: 100,
                //fill: 'red',
                fill: Konva.Util.getRandomColor(),
                //shadowBlur: 10
                draggable: true
            },

            configCircle: {
                x: sceneWidth / 2,
                y: sceneHeight / 2,
                radius: 90,
                fill: "yellow",
                stroke: "black",
                strokeWidth: 5,
                draggable: true,
                visible: true//false,
            },
            msg: {},
            msg2: {},
            src: "",
            ds: [],


            items: [],
            dragItemId: null,


        }
    },
    mounted() {
        this.items = generateItems();

        this.$nextTick(function () {
            layer = this.$refs.layer.getNode();
            stage = this.$refs.stage.getNode();
            window.addEventListener("resize", this.fitStageIntoParentContainer);
            this.fitStageIntoParentContainer();
        })
    },
    methods: {
        handleDragstart(e) {
            this.dragItemId = e.target.id();
            const item = this.items.find((i) => i.id === this.dragItemId);
            const index = this.items.indexOf(item);
            //e.target.zIndex(1000);
            //yellowBox.moveToTop();
            e.target.moveToTop();
        },
        handleDragend(e) {
            this.dragItemId = null;
        },


        onPaste(e) {
            alert(e.clipboardData);
        },
        downloadURI(uri, name) {
            var link = document.createElement('a');
            link.download = name;
            link.href = uri;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            //delete link;
        },
        onSave() {
            let stage = this.$refs.stage.getNode();
            var dataURL = stage.toDataURL({ pixelRatio: 0.5 });
            this.src = dataURL;
            this.ds.push(dataURL);
            //alert(dataURL);
            //this.downloadURI(dataURL, 'stage.png');
        },
        onStageClick(e) {
            let p = this.myGetMouse();
            this.msg = `mouse click ${JSON.stringify(p)}`;
            var clone = this.$refs.circle.getNode().clone();
            clone.x(p.x);
            clone.y(p.y);
            let layer = this.$refs.layer.getNode();
            layer.add(clone);
            this.msg2 = {
                x: clone.x(),
                y: clone.y(),
                w: clone.width(),
                h: clone.height(),
                d: clone.draggable(),
                v: clone.visible(),
                id: clone.id()
            };
        },
        onStageMouseMove(e) {
            let p = this.myGetMouse();
            this.msg = `mouse move ${JSON.stringify(p)}`;
        },
        onStageMouseenter(e) {
        },
        onStageMouseleave(e) {
        },

        myGetMouse() {
            let scale = stage.scale().x;
            var mousePos = stage.getPointerPosition();
            var x = mousePos.x;
            var y = mousePos.y;
            var xx = Math.round(x / scale);
            var yy = Math.round(y / scale);
            return { x: xx, y: yy };
        },
        fitStageIntoParentContainer() {
            var container = document.querySelector('#stage-parent');
            var containerWidth = container.offsetWidth;
            var scale = containerWidth / sceneWidth;
            stage.width(sceneWidth * scale);
            stage.height(sceneHeight * scale);
            stage.scale({ x: scale, y: scale });
            this.configKonva.width = sceneWidth * scale;
            this.configKonva.height = sceneHeight * scale;
        },
    }
}
</script>
  